.price-form {
  position: absolute;
  left: 0;
  top: 39px;
  width: calc((100vw / 3) - 137px);
  background: #f5f5f5;
  border: 1px solid #aaaeb3;
  border-radius: 5px;
  padding: 0px 10px;
  display: grid;
  grid-template-columns: auto 20px auto 50px 50px;
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.2);
  z-index: 22;
}


.price-sb:after {
  content: "";
  width: 0px;
  height: 0px;
  position: absolute;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid transparent;
  border-bottom: 10px solid #aaaeb3;
  left: 118px;
  top: -20px;
  z-index: 22;
}

.price-input {
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  background-color: white;
  padding: 20px 6px 4px 6px;
  margin-top: 8px;
}

.price-label {
  position: absolute;
  top: 10px;
  left: 8px;
  font-size: 12px;
  color: grey;
  z-index: 2;
}

.dash {
  font-size: 20px;
  padding: 0px 5px 0px 5px;
  color: grey;
  margin-top: 16px;
}

.reset-button {
  font-size: 12px;
  border: 1px solid #ccc;
  border-radius: 5px 5px 5px 5px;
  font-weight: 600;
  background-color: #fff;
  color: #000;
  display: block;
  margin: 8px 0px 10px 5px;
}
.reset-button:hover {
  background-color: #000;
  color: #fff;
  cursor: pointer;
}

.apply-button {
    font-size: 12px;
    border: 1px solid #ccc;
    border-radius: 5px 50% 50% 5px;
    font-weight: 600;
    background-color: #fff;
    color: #000;
    display: block;
    margin: 8px 0px 10px 5px;
  }
  .apply-button:hover {
    background-color: #000;
    color: #fff;
    cursor: pointer;
  }
