.header-container {
  margin: 1.25rem;
  padding: 1.25rem;
  border-radius: 0.5rem;
  background-color: #fff;
}

.details-gallery {
  position: relative;
}

.details-image {
  height: calc(100vh / 2);
  width: 100%;
  object-fit: cover;
}
.image-numbering {
  position: absolute;
  bottom: 1rem;
  left: 14rem;
  font-size: 1rem;
  color: #000;
  background-color: #fff;
  border: none;
  border-radius: 3px;
  padding: 2px 5px 3px 5px;
}
.details-carousel {
  overflow: hidden;
  white-space: nowrap;
  display: grid;
  grid-gap: 5px;
  position: relative;
}
.details-carousel:hover .right-arrow-thumbnails {
  visibility: visible;
}
.details-carousel:hover .left-arrow-thumbnails {
  visibility: visible;
}
.details-thumbnail {
  height: calc(100vh / 9);
  width: 100%;
  object-fit: cover;
  padding: 0px 5px 0px 0px;
  border-radius: 3px;
}
.details-thumbnail:hover {
  cursor: pointer;
}
.thumbnail-numbering {
  position: absolute;
  top: 3px;
  left: 3px;
  font-size: 16px;
  color: #000;
  background: #fff;
  border: none;
  border-radius: 3px;
  padding: 0px 3px 2px 3px;
}
.details-thumbnail:hover + span {
  cursor: pointer;
  color: #000;
  background: #ffc300;
}

.right-arrow {
  position: absolute;
  top: 50%;
  right: 3px;
  width: 35px;
  height: 35px;
  border: none;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.5);
  cursor: pointer;
}
.right-arrow:hover {
  background: rgba(255, 255, 255, 1);
}
.right-icon {
  padding-top: 3px;
  padding-left: 3px;
  width: 25px;
}
.right-icon:hover {
  filter: invert(17%) sepia(67%) saturate(7492%) hue-rotate(357deg)
    brightness(90%) contrast(128%);
}

.left-arrow {
  position: absolute;
  top: 50%;
  left: 3px;
  width: 35px;
  height: 35px;
  border: none;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  text-align: center;
}
.left-arrow:hover {
  background: rgba(255, 255, 255, 1);
}
.left-icon {
  padding-top: 3px;
  padding-right: 3px;
  width: 25px;
}
.left-icon:hover {
  filter: invert(17%) sepia(67%) saturate(7492%) hue-rotate(357deg)
    brightness(90%) contrast(128%);
}

.right-arrow-thumbnails {
  visibility: visible;
  position: absolute;
  top: 30%;
  right: 3px;
  width: 35px;
  height: 35px;
  border: none;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.5);
  cursor: pointer;
}
.right-arrow-thumbnails:hover {
  background: rgba(255, 255, 255, 1);
}
.left-arrow-thumbnails {
  visibility: visible;
  position: absolute;
  top: 30%;
  left: 3px;
  width: 35px;
  height: 35px;
  border: none;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  text-align: center;
}
.left-arrow-thumbnails:hover {
  background: rgba(255, 255, 255, 1);
}
