.listings-container {
  height: calc(100vh - 90px);
  width: calc(100vw / 3);
  overflow-y: scroll;
  overflow-x: hidden;
  position: relative;
  margin-top: 80px;
  padding-top: 10px;
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.2);
  background-color: #dcdcdc;
}

.list-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  grid-auto-rows: auto;
  padding: 0px 20px;
}

.ph-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  padding: 0px 10px;
}

.single-point {
  border: 1px solid black;
}
.cluster-group {
  /* border: 1px solid black; */
}
.cluster-leaf {
  border: 1px solid black;
}

.listing-counter-container {
  /* border: 1px solid #ccc; */
  position: fixed;
  left: calc(((100vw / 3) * 2) - 200px);
  bottom: 20px;
  z-index: 21;
  margin-left: auto;
  margin-right: auto;
}

.list-view-button {
  background-color: #000;
  color: #fff;
  border: none;
  border-radius: 0.3rem;
  padding: 0.375rem 0.75rem;
}
.list-view-button:hover:enabled {
  cursor: pointer;
}
.list-view-button:disabled {
  background-color: grey;
}