.user-dropdown {
  position: absolute;
  top: 39px;
  right: 0px;
  background: #f5f5f5;
  padding: 6px 12px;
  border: 1px solid #aaaeb3;
  border-radius: 5px;
  min-width: 150px;
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.2);
  z-index: 100;
}

.user-drop-button {
  width: 100%;
  font-size: 14px;
  padding: 3px 6px;
  border: 1px solid #aaaeb3;
  border-radius: 3px;
  font-weight: 400;
  background: #fff;
  color: #000;
  display: block;
  margin: 12px auto;
}
.user-drop-button:hover {
  background: #000;
  color: #fff;
  cursor: pointer;
}
.user-drop-button:hover .user-drop-link {
  background: #000;
  color: #fff;
  cursor: pointer;
}

.user-drop-link {
  color: #000;
  text-decoration: none;
}
.user-drop-link:hover {
  background: #000;
  color: #fff;
  cursor: pointer;
}